import {
  LOGIN_INFO,
  REGISTER_INFO,
  AUTH_INFO,
  ADD_CART,
  REMOVE_CART,
} from "../_action/types";

export default function userData(state = {}, action) {
  //action > user_action return 
  //      switch 
  switch (action.type) {
    case LOGIN_INFO:
      return { ...state, loginSuccess: action.payload };
      break;

    case REGISTER_INFO:
      return { ...state, register: action.payload };
      break;

    case AUTH_INFO:
      return { ...state, userData: action.payload };
      break;

    case ADD_CART:
      if (action.payload) {
        alert("  .");
        return { ...state };
        break;
      } else {
        alert(" .");
        return {
          ...state,
          userData: {
            ...state.userData,
            cart: action.payload.cart,
          },
        };
        break;
      }

    case REMOVE_CART:
      return {
        ...state,
        userData: {
          ...state.userData,
          cart: action.payload,
        },
      };
      break;

    //payload    

    default:
      return state;
  }
}
