import React, { useEffect } from "react";
import { useLocation } from "react-router";
import { useNavigate } from "react-router-dom";

import "./PaymentResult.css";

function PaymentResult() {
  const nav = useNavigate();
  const { state } = useLocation();

  useEffect(() => {
    if (state === null) {
      nav("/");
    }
  }, []);

  return (
    <div className="page">
      <div className="purchase-procedure">
         &gt;  &gt; <strong></strong>
      </div>

      <div className="purchase-procedure-totalPrice">{`  ${parseInt(
        state.price,
        10
      ).toLocaleString()}`}</div>

      <div className="paymentResult-goBtn">
        <button onClick={() => nav("/purchaseHistory")}></button>
        <button onClick={() => nav("/")}> </button>
      </div>

      <div className="checkOut-section">
        <div></div>
        <hr />
        <div>
          <div className="Upload-section">
            <div></div>
            <div>{state.shippingInfo.name}</div>
          </div>
          <div className="Upload-section">
            <div></div>
            <div>{state.shippingInfo.phone}</div>
          </div>
          <div className="Upload-section">
            <div></div>
            <div>{state.shippingInfo.adress}</div>
          </div>
          <div className="Upload-section">
            <div></div>
            <div>{state.shippingInfo.req}</div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default PaymentResult;
